
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.currentLocation {
  /* margin:-11px 0 0 -11px; */
  display: block;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  /* background: #010b0d; */
  cursor: pointer;
  box-shadow: 0 0 0 rgba(100, 255, 218, 0.4);
  -webkit-animation: pulse 2s infinite;
          animation: pulse 2s infinite;
}
.currentLocation svg {
  margin: -4px;
  color: white;
}

.currentLocation:hover {
  -webkit-animation: none;
          animation: none;
}

@-webkit-keyframes pulse {
  0% {
    -webkit-box-shadow: 0 0 0 0 rgba(100, 255, 218, 0.4);
  }
  70% {
    -webkit-box-shadow: 0 0 0 50px rgba(100, 255, 218, 0);
  }
  100% {
    -webkit-box-shadow: 0 0 0 0 rgba(100, 255, 218, 0);
  }
}
@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(100, 255, 218, 0.4);
  }
  70% {
    box-shadow: 0 0 0 100px rgba(100, 255, 218, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(100, 255, 218, 0);
  }
}
html, body, #root, #wrapper {
  height: 100%;
  background: #eee;
}
body {
  margin: 0;
  padding: 0;
  background: #eeeeee;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
#wrapper {
  height: 100%;
  box-sizing: border-box;
  padding: 64px 0 0;
}
.main,
.main > div {
  height: 100%;
}

